@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap');

*, *::before, *::after{
   box-sizing: border-box;
  }
  
*{
   margin: 0;
 }

body{
	font-family:'Nunito', sans-serif;
	background: #252525;
}

.home{
	margin-top: 3rem;
}
.container{
   padding: 0 1rem;
}

.header{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding-top: 2rem;
    background: #252525;
	font-size: 1.45rem;
	color: white;
	position: fixed;
	top: 0;
	z-index: 1;
	width: 95vw;
}
.search, .search2{
	position: fixed;
	right: 22%;
	display: inline-block;
	text-align: center;
	align-self: flex-end;
	background: #3B3B3B;
	width: 50px;
	height: 50px;
	border-radius: 20%;
}
.search2{
	right: 4%;
}
.search-icon{
	position: relative;
	top: 25%;
}
.searchBar{
	background: #E5E5E5;
	border: none;
	outline: 0;
	border-radius: 5px;
	width: 100%;
	padding: 0.25rem 0.25rem;
	font-size: 1.25rem;
}